// ----------------------------------------------------------------------------
// Header specific styles, variables, placeholders, mixins and functions
// @see components/organisms/Header.jsx
// @author Lexus Drumgold
// ----------------------------------------------------------------------------

// Variables
$header-height: auto;
$header-padding: size(40) 0;
$header-width: 100vw;

// Styles

// Position header absolutely
// Make header full width
// Position header on top of the other content
// Add space on the top and bottom of header
.ado-header {
  @include absolute(0, null, null, 0);
  @include respond-to($breakpoint-schmedium) {
    background-color: $black;
    border-bottom: size(1) solid $accent-light;
    position: relative;
    z-index: 0; }


  @include size($header-width, $header-height);

  height: max-content;
  z-index: 10;

  > .ada-container {
    @include respond-to($breakpoint-schmedium) {
      @include flex(row, center, center);

      padding-bottom: size(20); }


    padding: $header-padding;

    .adm-logo {
      @include constrain(size(137.5));
      @include respond-to($breakpoint-schmedium) {
        @include constrain(size(150)); } } }

  &.ui-sticky {
    background-color: $light;
    border-bottom: $border;
    position: fixed;

    > .ada-container {
      padding: size(16) 0; } } }
