// ----------------------------------------------------------------------------
// Footer specific styles, variables, placeholders, mixins and functions
// @see components/organisms/Footer.jsx
// @author Lexus Drumgold
// ----------------------------------------------------------------------------

// Variables
$footer-border: size(0.5) solid $light;
$footer-height: auto;
$footer-padding: size(40) 0;
$footer-width: 100vw;
$footer-logo-spacing: size(16);

// Styles

// Make footer full width
// Add space on the top and bottom of footer
.ado-footer {
  @include sticky(null, 0, 0, null);
  @include constrain($footer-width);
  @include flex(row, center);
  @include respond-to($breakpoint-schmedium) {
    border-top: size(1) solid $accent-light; }


  background-color: $black;
  padding: size(32) 0 size(32) 2.5%;

  .adm-logo {
    @include constrain(size(137.5));
    @include respond-to($breakpoint-schmedium) {
      @include constrain(size(65)); }


    background-color: $black;
    border-right: size(1.5) solid $light;
    margin-right: $footer-logo-spacing;
    padding-right: $footer-logo-spacing;
    z-index: 5; }

  &.multimedia-border {
    border-top: $footer-border; } }
