// ----------------------------------------------------------------------------
// Loading screen specific styles, variables, placeholders, mixins and functions
// @see components/screens/Loading.jsx
// @author Lexus Drumgold
// ----------------------------------------------------------------------------

// Variables
$loading-screen-height: 100%;
$loading-screen-width: 100%;

// Styles

// Position inner content in the center
.ads-loading {
  @include flex(row, center, center);
  @include size($loading-screen-width, $loading-screen-height);

  > .ada-container {
    @include flex(row, center, center);

    .adm-logo {
      @include respond-to($breakpoint-xxl, min) {
        width: size(800); }


      @include respond-to($breakpoint-xxxl, min) {
        font-size: size(1000); }


      width: size(600); } } }
