// Variables
$deck-height: 100%;
$deck-padding: size(20) 3.75%;
$deck-width: 100%;

.ado-deck {
  @include constrain($deck-width);
  @include flex(column, center, space-between);
  @include respond-to($breakpoint-schmedium) {
    align-items: flex-start;
    overflow-y: scroll;
    padding: $deck-padding; }


  background: {
    position: center center;
    repeat: no-repeat;
    size: cover; } }

